.Input{
    width:100%;
    padding:10px;
    box-sizing: border-box;
}

.Label{
    font-weight: bold;
    font-family: inherit;
    display: block;
    margin-bottom:8px;
}

.InputElement{
    outline: none;
    border: 1px solid #ccc;
    background-color:white;
    padding: 6px;
    font:inherit;
    display: block;
    width: 100%;
    box-sizing: border-box;

}
.InputElement:focus{
    outline:none;
    background-color: #ccc;
}